.header{
  color: #fff;
  font-weight: $bold-weight;
  display: flex;
  justify-content: center;
  align-items: stretch;

  .toggle-nav{
    display: none;
    align-items: center;
    font-size: 22px;
    span{
      color: $secondary-color;
      margin-top: -2px;
      padding-right: 5px;
    }
  }
  
  .logo{
    font-size: 40px;
  }

  .nav{
    display: flex;

    ul{
      display: flex;
      align-items: stretch;

      li{
        display: flex;

        a{
          font-weight: $bold-weight;
          padding: 5px 20px;
          display: flex;
          align-items: center;
          
          &:hover{
            background: rgba(95, 94, 94, 0.1);
          }
        }
      }
    }
  }
}



@media all and (max-width: $tablet-breakpoint){
  .header{
    // justify-content: space-between;
    flex-wrap: wrap;

    .logo{
      flex: 1;
      display: flex;
      justify-content: center;
      padding-right: 20px;
    }

    .toggle-nav{
      display: flex;
      padding-left: 15px;
    }

    .nav{
      max-height: 0;
      transition: all 1s 0s linear;
      overflow: hidden;
      margin-bottom: 50px;
      flex: 1 1 100%;
        flex-direction: column;
        text-align: center;

      &.open{
        max-height: 800px;
      }

      ul {
        flex-direction: column;
        border-top: 1px solid rgba(255,255,255,0.5);
        line-height: 1.7em;
        a{
          flex-basis: 100%;
          justify-content: center;
          padding: 15px 10px;
        }
      }
    }
    }
}